import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import i18n from './utils/i18n'
import gameresult from "./utils/result";
import gamerequest from "./utils/request";
import c from "./utils/c";
import "./style/page.css"
import 'animate.css';
import 'weui'

Vue.prototype.$gameresult = gameresult
Vue.prototype.$gamerequest = gamerequest
Vue.prototype.$c = c
Vue.config.productionTip = false

const FastClick = require('fastclick')
FastClick.attach(document.body)

import countTo from 'vue-count-to'
Vue.component('countTo', countTo)

import VueQRCode from 'vue-qrcode-component'
Vue.component('qr-code', VueQRCode)

import {
  Slider, NoticeBar, ActionSheet,
  Stepper, Switch, Col, Row, Toast,
  Dialog, Badge, Tag, Icon, Picker
} from 'vant'

Vue.use(Picker);
Vue.use(Slider)
Vue.use(NoticeBar)
Vue.use(ActionSheet)
Vue.use(Stepper)
Vue.use(Switch)
Vue.use(Col)
Vue.use(Row)
Vue.use(Toast)
Vue.use(Dialog)
Vue.use(Badge)
Vue.use(Tag)
Vue.use(Icon)

new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app')
