<template>
  <div class="page">
    <div class="page__hd page__hd1">
      <div class="page__title page__title1" @click="open">
        <div class="fadeInDown1">{{ jackpotAddress }}</div>
      </div>
      <div class="weui-flex" style="margin-top: 10px">
        <div class="weui-flex__item weui-flex__item1">
          <img width="36" src="../images/trx.png" class="flipInY" /><br />
          <div class="fadeInDown2">
            <countTo :startVal="startTrx" :endVal="endTrx" :duration="2000" :decimals="decimals"></countTo>
          </div>
        </div>
        <div class="weui-flex__item weui-flex__item1">
          <img width="36" src="../images/usdt.png" class="flipInY" /><br />
          <div class="fadeInDown2">
            <countTo :startVal="startUsdt" :endVal="endUsdt" :duration="2000" :decimals="decimals"></countTo>
          </div>
        </div>
      </div>
    </div>

    <div style="font-weight: bold">
      <van-notice-bar color="#333" background="#f5c822" left-icon="fire-o">
        {{ AD }}
      </van-notice-bar>
    </div>
    <div style="background: #fff">
      <div class="weui-grids">
        <div class="weui-grid" @click="handle(1)">
          <div class="flipInY">
            <div class="weui-grid__icon tada">
              <img src="../images/coin.png" />
            </div>
            <p class="weui-grid__label">{{ DS }}</p>
          </div>
        </div>
        <div class="weui-grid" @click="handle(2)">
          <div class="flipInY">
            <div class="weui-grid__icon">
              <img src="../images/metal.png" />
            </div>
            <p class="weui-grid__label">{{ DX }}</p>
          </div>
        </div>
      </div>
      <div class="weui-grids">
        <div class="weui-grid" @click="handle(3)">
          <div class="flipInY">
            <div class="weui-grid__icon">
              <img src="../images/ace.png" />
            </div>
            <p class="weui-grid__label">{{ BJL }}</p>
          </div>
        </div>
        <div class="weui-grid" @click="handle(4)">
          <div class="flipInY">
            <div class="weui-grid__icon">
              <img src="../images/poker.png" />
            </div>
            <p class="weui-grid__label">{{ DN }}</p>
          </div>
        </div>
      </div>
      <div class="weui-grids">
        <div class="weui-grid" @click="handle(9)">
          <div class="flipInY">
            <div class="weui-grid__icon">
              <img src="../images/scroll.png" />
            </div>
            <p class="weui-grid__label">{{ Query }}</p>
          </div>
        </div>
        <div class="weui-grid" @click="handle(0)">
          <div class="flipInY">
            <div class="weui-grid__icon">
              <img src="../images/envelope.png" />
            </div>
            <p class="weui-grid__label">{{ Service }}</p>
          </div>
        </div>
      </div>
    </div>

    <div class="weui-footer weui-footer_fixed-bottom">
      <p class="weui-footer__text">CoinGame &copy; 2020-2022 </p>
    </div>

    <van-dialog v-model="show" :confirmButtonText="queryText" confirmButtonColor="#f5c822" @confirm="startQuery" :close-on-click-overlay="true">
      <div class="weui-cell__bd" style="padding: 20px">
        <input class="weui-input" style="text-align: center" :placeholder="orderHash" v-model="value" />
      </div>
    </van-dialog>
  </div>
</template>

<script>
export default {
  name: "HomeView",
  data() {
    return {
      tronWeb: null,
      jadd: process.env.VUE_APP_JADD,
      uadd: process.env.VUE_APP_UADD,

      tronurl: this.$t("home.tron"),
      wallet: this.$t("home.wallet"),
      telegram: this.$t("home.telegram"),

      jackpotAddress: "",
      walletAddress: "",
      trxbalance: 0,
      usdtbalance: 0,

      AD: this.$t("home.AD"),
      DX: this.$t("home.DX"),
      DS: this.$t("home.DS"),
      BJL: this.$t("home.BJL"),
      DN: this.$t("home.DN"),
      Query: this.$t("home.query"),
      Service: this.$t("home.service"),

      startUsdt: 0,
      endUsdt: 0,
      startTrx: 0,
      endTrx: 0,
      decimals: 2,
      sum: 1000000,

      show: false,
      value: "",
      orderHash: this.$t("home.orderHash"),
      queryText: this.$t("home.queryText"),
      message1: this.$t("home.message1"),
      message2: this.$t("home.message2"),
      message3: this.$t("home.message3"),
      message4: this.$t("home.message4"),
      message5: this.$t("home.message5"),

      Inval: null,
      InvalBalance: null,
    };
  },
  beforeCreate() {
    if (this.$route.query.l == "en_US") this.$i18n.locale = "en_US";
    if (this.$route.query.l == "zh_CN") this.$i18n.locale = "zh_CN";
  },
  mounted() {
    this.initHome();
  },
  beforeDestroy() {
    clearInterval(this.Inval);
    clearInterval(this.InvalBalance);
    this.$toast.clear();
  },
  methods: {
    initHome: function () {
      this.jackpotAddress = this.getShowAddress(this.jadd);
      this.getBalance(this.jadd);
      this.startInvalBalance();
      this.linkWallet();
    },
    open: function () {
      window.open(this.$gameresult.getTRONUrl(this.$i18n.locale) + "address/" + this.jadd, "_blank");
    },
    service: function () {
      window.open(this.telegram, "_blank");
    },
    startQuery: function () {
      if (this.value.length == 0) return;
      if (this.value.length < 32) this.$toast({ message: this.message1, position: "bottom" });
      else this.$router.push({ path: "/query?txid=" + this.value + "&l=" + this.$i18n.locale });
    },
    handle: function (h) {
      switch (h) {
        case 1:
          if (this.walletAddress != "") this.$router.push({ path: "/dsgame?l=" + this.$i18n.locale });
          else this.$router.push({ path: "/manual?l=" + this.$i18n.locale + "&g=ds" });
          break;
        case 2:
          if (this.walletAddress != "") this.$router.push({ path: "/dxgame?l=" + this.$i18n.locale });
          else this.$router.push({ path: "/manual?l=" + this.$i18n.locale + "&g=dx" });
          break;
        case 3:
          if (this.walletAddress != "") this.$router.push({ path: "/bjlgame?l=" + this.$i18n.locale });
          else this.$router.push({ path: "/manual?l=" + this.$i18n.locale + "&g=bjl" });
          break;
        case 4:
          if (this.walletAddress != "") this.$router.push({ path: "/dngame?l=" + this.$i18n.locale });
          else this.$router.push({ path: "/manual?l=" + this.$i18n.locale + "&g=dn" });
          break;
        case 9:
          this.value = "";
          if (this.walletAddress != "") this.show = true;
          else this.$dialog.alert({
            message: this.$t("login.message1"),
            confirmButtonText: this.$t("login.message2"),
            confirmButtonColor: "#f5c822",
          }).then(() => {
            window.open(this.wallet, "_blank");
          });
          break;
        case 0:
          this.service();
          break;
        default:
          break;
      }
    },
    getShowAddress: function (add) {
      return add.substr(0, 6) + "********" + add.substr(-6, 6);
    },
    getShowNum: function (num) {
      return Math.floor(num * 100) / 100;
    },
    linkWallet: function () {
      var i = 0;
      this.Inval = setInterval(() => {
        if (window.tronWeb != undefined) {
          var wadd = window.tronWeb.defaultAddress.base58;
          this.walletAddress = wadd ? wadd : "";

          if (this.walletAddress) {
            window.clearInterval(this.Inval);
            this.tronWeb = window.tronWeb;
          }
        }
        if (i > 5) window.clearInterval(this.Inval);
        i++;
      }, 1000);
    },
    getBalance: function (add) {
      this.$gamerequest
        .getAccount(add)
        .then((response) => {
          var data = response.data.data;
          data.forEach((element) => {
            if (element.tokenId == "_") {
              this.trxbalance = element.balance / this.sum;
              this.startTrx = this.endTrx;
              this.endTrx = this.getShowNum(this.trxbalance);
            }
            if (element.tokenId == this.uadd) {
              this.usdtbalance = element.balance / this.sum;
              this.startUsdt = this.endUsdt;
              this.endUsdt = this.getShowNum(this.usdtbalance);
            }
          });
        });
    },
    startInvalBalance() {
      this.InvalBalance = setInterval(() => {
        this.getBalance(this.jadd);
      }, 60000);
    },
  },
};
</script>
