  background: #333;
  padding: 30px 10px 20px 10px;
}

.page__title1 {
  text-align: center;
  color: #f5c822;
  font-weight: bold;
  font-size: 24px;
}

.weui-flex__item1 {
  text-align: center;
  color: #fff;
  font-weight: bold;
  font-size: 16px;
}

.weui-grid {
  position: relative;
  float: left;
  width: 49.99999999%;
  box-sizing: border-box;
  padding: 15px 10px;
}

.weui-grid__icon {
  width: 64px;
