<template>
  <div class="page">
    <div style="position: absolute; left: 10px; top: 5px;font-size: 20px;" @click="home">
      <van-icon name="revoke" color="#aaa" class="animate__animated animate__fadeInLeft" />
    </div>
    <div style="position: absolute; right: 10px; top: 5px;font-size: 20px;" @click="showRule">
      <van-icon name="question-o" color="#aaa" class="animate__animated animate__fadeInRight" />
    </div>
    <div class="page__hd page__hd1">
      <div class="page__title page__title1 fadeInDown1" @click="open">
        {{ jackpotAddress }}
      </div>
      <div class="weui-flex" style="margin-top: 10px">
        <div class="weui-flex__item weui-flex__item1">
          <img width="36" src="../images/trx.png" class="flipInY" /><br />
          <div class="fadeInDown2">
            <countTo :startVal="startTrx" :endVal="endTrx" :duration="2000" :decimals="decimals"></countTo>
          </div>
        </div>
        <div class="weui-flex__item weui-flex__item1">
          <img width="36" src="../images/usdt.png" class="flipInY" /><br />
          <div class="fadeInDown2">
            <countTo :startVal="startUsdt" :endVal="endUsdt" :duration="2000" :decimals="decimals"></countTo>
          </div>
        </div>
      </div>
    </div>

    <div style="font-weight: bold">
      <van-notice-bar color="#333" background="#f5c822" left-icon="fire-o" mode="link" @click="service">
        {{ AD }}
      </van-notice-bar>
    </div>

    <div style="background: #fff;">
      <div class="weui-cell weui-cell_access" style="border-top:0px" @click.stop="showRule">
        <span class="weui-cell__bd">
          <div class="fadeInDown2" style="font-weight: bold;">{{ gameName }}</div>
          <div class="weui-cell__desc">{{ gameRule }}</div>
        </span>
      </div>
      <div class="weui-grids">
        <div class="weui-grid">

          <div class="weui-grid__icon flipInY">
            <div style="padding:32px">
              <qr-code :size=192 :text="url" color="#333" bg-color="#f5c822" error-level="M"></qr-code>
              <div style="font-size: 14px;font-weight: bold;">TRON</div>
            </div>
          </div>
        </div>
      </div>
      <div class="weui-cell weui-cell_access" style="border-top:0px" @click.stop="copyAddress">
        <span class="weui-cell__bd">
          <div class="fadeInDown2" style="font-weight: bold;">{{ address }}</div>
          <div class="weui-cell__desc">{{ betAddress }}</div>
        </span>
      </div>
    </div>

    <van-dialog v-model="ruleShow" :title="ruleName" @cancel="downWallet" @confirm="ok" :confirmButtonText="okText" :cancelButtonText="downloadText" confirmButtonColor="#f5c822" cancelButtonColor="#f5c822" :showCancelButton="true">
      <div class="content" v-html="rule"></div>
    </van-dialog>
  </div>
</template>

<script>
export default {
  name: "ManualView",
  data() {
    return {
      tronWeb: null,
      jadd: process.env.VUE_APP_JADD,
      uadd: process.env.VUE_APP_UADD,

      tronurl: this.$t("home.tron"),
      wallet: this.$t("home.wallet"),
      telegram: this.$t("home.telegram"),

      jackpotAddress: "",
      walletAddress: "",
      trxbalance: 0,
      usdtbalance: 0,

      AD: this.$t("home.AD"),

      startUsdt: 0,
      endUsdt: 0,
      startTrx: 0,
      endTrx: 0,
      decimals: 2,
      sum: 1000000,

      url: "",
      betAddress: this.$t("game.betAddress"),
      address: " ",
      hash: this.$route.query.txid,

      Inval: null,
      InvalBalance: null,

      rule: "",
      ruleShow: false,
      ruleName: this.$t("game.ruleName"),
      okText: this.$t("game.okText"),
      downloadText: this.$t("game.download"),

      gameName: "",
      gameRule: "",
    };
  },
  beforeCreate() {
    if (this.$route.query.l == "en_US") this.$i18n.locale = "en_US";
    if (this.$route.query.l == "zh_CN") this.$i18n.locale = "zh_CN";
  },
  mounted() {
    this.initManual();
  },
  beforeDestroy() {
    clearInterval(this.InvalBalance);
    this.$toast.clear();
  },
  methods: {
    open: function () {
      window.open(this.$gameresult.getTRONUrl(this.$i18n.locale) + "address/" + this.jadd, "_blank");
    },
    service: function () {
      window.open(this.telegram, "_blank");
    },
    home: function () {
      this.$router.push({ path: "/home?l=" + this.$i18n.locale });
    },
    downWallet: function () {
      window.open(this.wallet, "_blank");
    },
    ok: function () {
      this.ruleShow = false;
    },
    showRule: function () {
      this.ruleShow = true;
    },
    initManual: function () {
      this.url = "http://" + window.location.host + "/";
      this.initRule();
      this.jackpotAddress = this.getShowAddress(this.jadd);
      this.getBalance(this.jadd);
      this.startInvalBalance();
      setTimeout(() => { this.showRule() }, 2500)
    },
    initRule: function () {
      switch (this.$route.query.g) {
        case "ds":
          this.address = process.env.VUE_APP_DSADD;
          this.gameName = this.$t("home.DS");
          this.gameRule = this.$t("game.odd") + " & " + this.$t("game.even");
          this.betAddress = "TRX & USDT" + " | " + this.betAddress + " | " + this.$t("game.copy");
          this.rule = this.$t("game.dsrule");
          break;
        case "dx":
          this.address = process.env.VUE_APP_DXADD;
          this.gameName = this.$t("home.DX");
          this.gameRule = this.$t("game.big") + " & " + this.$t("game.small");
          this.betAddress = "TRX & USDT" + " | " + this.betAddress + " | " + this.$t("game.copy");
          this.rule = this.$t("game.dxrule");
          break;
        case "dn":
          this.address = process.env.VUE_APP_DNADD;
          this.gameName = this.$t("home.DN");
          this.gameRule = this.$t("game.banker") + " & " + this.$t("game.player") + " & " + this.$t("game.tie");
          this.betAddress = "TRX & USDT" + " | " + this.betAddress + " | " + this.$t("game.copy");
          this.rule = this.$t("game.dnrule");
          break;
        case "bjl":
          this.address = process.env.VUE_APP_BJLADD;
          this.gameName = this.$t("home.BJL");
          this.gameRule = this.$t("game.banker") + " & " + this.$t("game.player") + " & " + this.$t("game.tie");
          this.betAddress = "TRX & USDT" + " | " + this.betAddress + " | " + this.$t("game.copy");
          this.rule = this.$t("game.bjlrule");
          break;
        default:
          this.$router.push({ path: "/home?l=" + this.$i18n.locale });
          break;
      }
    },
    getShowAddress: function (add) {
      return add.substr(0, 6) + "********" + add.substr(-6, 6);
    },
    getShowNum: function (num) {
      return Math.floor(num * 100) / 100;
    },
    copyAddress() {
      let oInput = document.createElement("input");
      oInput.value = this.address;
      document.body.appendChild(oInput);
      oInput.select();
      document.execCommand("Copy");
      oInput.remove();
      this.$toast({ message: this.$t("game.copys"), position: "bottom" });
    },
    startInvalBalance() {
      this.InvalBalance = setInterval(() => {
        this.getBalance(this.jadd);
      }, 60000);
    },
    getBalance: function (add) {
      this.$gamerequest
        .getAccount(add)
        .then((response) => {
          var data = response.data.data;
          data.forEach((element) => {
            if (element.tokenId == "_") {
              this.trxbalance = element.balance / this.sum;
              this.startTrx = this.endTrx;
              this.endTrx = this.getShowNum(this.trxbalance);
            }
            if (element.tokenId == this.uadd) {
              this.usdtbalance = element.balance / this.sum;
              this.startUsdt = this.endUsdt;
              this.endUsdt = this.getShowNum(this.usdtbalance);
            }
          });
        });
    },
  },
};
</script>
