function getNNResult(dn) {
    var barr = dn.split("");
    var sa = new Array(barr[0], barr[1], barr[2], barr[3], barr[4]);

    for (let i = 0; i < sa.length; i++) {
        if (isNaN(sa[i])) {
            sa[i] = parseInt("10");
        }
        sa[i] = parseInt(sa[i])
    }

    var a = -1;
    var r = "n|" + a + "|" + barr[0] + "," + barr[1] + "," + barr[2] + "," + barr[3] + "," + barr[4];
    if ((sa[0] + sa[1] + sa[2]) % 10 == 0) {
        a = (sa[3] + sa[4]) % 10;
        a = a == 0 ? 10 : a;
        r = "y|" + a + "|" + barr[0] + "," + barr[1] + "," + barr[2] + " + " + barr[3] + "," + barr[4];
        return r;
    }
    if ((sa[0] + sa[1] + sa[3]) % 10 == 0) {
        a = (sa[2] + sa[4]) % 10;
        a = a == 0 ? 10 : a;
        r = "y|" + a + "|" + barr[0] + "," + barr[1] + "," + barr[3] + " + " + barr[2] + "," + barr[4];
        return r;
    }
    if ((sa[0] + sa[1] + sa[4]) % 10 == 0) {
        a = (sa[2] + sa[3]) % 10;
        a = a == 0 ? 10 : a;
        r = "y|" + a + "|" + barr[0] + "," + barr[1] + "," + barr[4] + " + " + barr[2] + "," + barr[3];
        return r;
    }
    if ((sa[0] + sa[2] + sa[3]) % 10 == 0) {
        a = (sa[1] + sa[4]) % 10;
        a = a == 0 ? 10 : a;
        r = "y|" + a + "|" + barr[0] + "," + barr[2] + "," + barr[3] + " + " + barr[1] + "," + barr[4];
        return r;
    }
    if ((sa[0] + sa[2] + sa[4]) % 10 == 0) {
        a = (sa[1] + sa[3]) % 10;
        a = a == 0 ? 10 : a;
        r = "y|" + a + "|" + barr[0] + "," + barr[2] + "," + barr[4] + " + " + barr[1] + "," + barr[3];
        return r;
    }
    if ((sa[0] + sa[3] + sa[4]) % 10 == 0) {
        a = (sa[1] + sa[2]) % 10;
        a = a == 0 ? 10 : a;
        r = "y|" + a + "|" + barr[0] + "," + barr[3] + "," + barr[4] + " + " + barr[1] + "," + barr[2];
        return r;
    }
    if ((sa[1] + sa[2] + sa[3]) % 10 == 0) {
        a = (sa[0] + sa[4]) % 10;
        a = a == 0 ? 10 : a;
        r = "y|" + a + "|" + barr[1] + "," + barr[2] + "," + barr[3] + " + " + barr[0] + "," + barr[4];
        return r;
    }
    if ((sa[1] + sa[2] + sa[4]) % 10 == 0) {
        a = (sa[0] + sa[3]) % 10;
        a = a == 0 ? 10 : a;
        r = "y|" + a + "|" + barr[1] + "," + barr[2] + "," + barr[4] + " + " + barr[0] + "," + barr[3];
        return r;
    }
    if ((sa[1] + sa[3] + sa[4]) % 10 == 0) {
        a = (sa[0] + sa[2]) % 10;
        a = a == 0 ? 10 : a;
        r = "y|" + a + "|" + barr[1] + "," + barr[3] + "," + barr[4] + " + " + barr[0] + "," + barr[2];
        return r;
    }
    if ((sa[2] + sa[3] + sa[4]) % 10 == 0) {
        a = (sa[0] + sa[1]) % 10;
        a = a == 0 ? 10 : a;
        r = "y|" + a + "|" + barr[2] + "," + barr[3] + "," + barr[4] + " + " + barr[0] + "," + barr[1];
        return r;
    }
    return r;
}

function getDNResult(dnx, dnz) {
    if (dnx[0] == "n" && dnz[0] == "n") {
        return "T";
    }
    if (dnx[0] == "y" && dnz[0] == "n") {
        return "P";
    }
    if (dnx[0] == "n" && dnz[0] == "y") {
        return "B";
    }
    if (dnx[0] == "y" && dnz[0] == "y") {
        if (parseInt(dnx[1]) == parseInt(dnz[1])) {
            return "T";
        }
        if (parseInt(dnx[1]) > parseInt(dnz[1])) {
            return "P";
        }
        if (parseInt(dnx[1]) < parseInt(dnz[1])) {
            return "B";
        }
    }
    return "";
}

function getBJLAllResult(blockHash) {

    var pArr = blockHash.substr(-6, 3).split("");
    var bArr = blockHash.substr(-3, 3).split("");

    var p = new Array(pArr[0], pArr[1], pArr[2]);
    var b = new Array(bArr[0], bArr[1], bArr[2]);

    for (let i = 0; i < b.length; i++) {
        if (isNaN(p[i])) {
            p[i] = parseInt("10");
        }
        if (isNaN(b[i])) {
            b[i] = parseInt("10");
        }
        p[i] = parseInt(p[i])
        b[i] = parseInt(b[i])
    }

    switch ((p[0] + p[1]) % 10) {
        case 0:
            return getBJLResult(p, b, pArr, bArr);
        case 1:
            return getBJLResult(p, b, pArr, bArr);
        case 2:
            return getBJLResult(p, b, pArr, bArr);
        case 3:
            return getBJLResult(p, b, pArr, bArr);
        case 4:
            return getBJLResult(p, b, pArr, bArr);
        case 5:
            return getBJLResult(p, b, pArr, bArr);
        case 6:
            if ((b[0] + b[1]) % 10 == 6) {
                return "T|" + pArr[0] + "," + pArr[1] + "|" + (p[0] + p[1]) % 10 + "|" + bArr[0] + "," + bArr[1] + "|" + (b[0] + b[1]) % 10;
            }
            else if ((b[0] + b[1]) % 10 > 6) {
                return "B|" + pArr[0] + "," + pArr[1] + "|" + (p[0] + p[1]) % 10 + "|" + bArr[0] + "," + bArr[1] + "|" + (b[0] + b[1]) % 10;
            }
            else {
                return getBJLResultB(p, b, pArr, bArr);
            }
        case 7:
            if ((b[0] + b[1]) % 10 == 7) {
                return "T|" + pArr[0] + "," + pArr[1] + "|" + (p[0] + p[1]) % 10 + "|" + bArr[0] + "," + bArr[1] + "|" + (b[0] + b[1]) % 10;
            }
            else if ((b[0] + b[1]) % 10 > 7) {
                return "B|" + pArr[0] + "," + pArr[1] + "|" + (p[0] + p[1]) % 10 + "|" + bArr[0] + "," + bArr[1] + "|" + (b[0] + b[1]) % 10;
            }
            else if ((b[0] + b[1]) % 10 == 6) {
                return "P|" + pArr[0] + "," + pArr[1] + "|" + (p[0] + p[1]) % 10 + "|" + bArr[0] + "," + bArr[1] + "|" + (b[0] + b[1]) % 10;
            }
            else {
                return getBJLResultB(p, b, pArr, bArr);
            }
        case 8:
            if ((b[0] + b[1]) % 10 == 8) {
                return "T|" + pArr[0] + "," + pArr[1] + "|" + (p[0] + p[1]) % 10 + "|" + bArr[0] + "," + bArr[1] + "|" + (b[0] + b[1]) % 10;
            }
            else if ((b[0] + b[1]) % 10 > 8) {
                return "B|" + pArr[0] + "," + pArr[1] + "|" + (p[0] + p[1]) % 10 + "|" + bArr[0] + "," + bArr[1] + "|" + (b[0] + b[1]) % 10;
            }
            else {
                return "P|" + pArr[0] + "," + pArr[1] + "|" + (p[0] + p[1]) % 10 + "|" + bArr[0] + "," + bArr[1] + "|" + (b[0] + b[1]) % 10;
            }
        case 9:
            if ((b[0] + b[1]) % 10 == 9) {
                return "T|" + pArr[0] + "," + pArr[1] + "|" + (p[0] + p[1]) % 10 + "|" + bArr[0] + "," + bArr[1] + "|" + (b[0] + b[1]) % 10;
            }
            else {
                return "P|" + pArr[0] + "," + pArr[1] + "|" + (p[0] + p[1]) % 10 + "|" + bArr[0] + "," + bArr[1] + "|" + (b[0] + b[1]) % 10;
            }
        default:
            return "";
    }
}

function getBJLResult(p, b, pArr, bArr) {
    if ((b[0] + b[1]) % 10 == 3) {
        if (p[2] == 8) {
            return getBJLResultP(p, b, pArr, bArr);
        }
        else {
            return getBJLResultA(p, b, pArr, bArr);
        }
    }
    else if ((b[0] + b[1]) % 10 == 4) {
        if (p[2] == 0 || p[2] == 1 || p[2] == 8 || p[2] == 9) {
            return getBJLResultP(p, b, pArr, bArr);
        }
        else {
            return getBJLResultA(p, b, pArr, bArr);
        }
    }
    else if ((b[0] + b[1]) % 10 == 5) {
        if (p[2] == 0 || p[2] == 1 || p[2] == 2 || p[2] == 3 || p[2] == 8 || p[2] == 9) {
            return getBJLResultP(p, b, pArr, bArr);
        }
        else {
            return getBJLResultA(p, b, pArr, bArr);
        }
    }
    else if ((b[0] + b[1]) % 10 == 6) {
        if (p[2] == 6 || p[2] == 7) {
            return getBJLResultA(p, b, pArr, bArr);
        }
        else {
            return getBJLResultP(p, b, pArr, bArr);
        }
    }
    else if ((b[0] + b[1]) % 10 == 7) {
        return getBJLResultP(p, b, pArr, bArr);
    }
    else if ((b[0] + b[1]) % 10 > 7) {
        return "B|" + pArr[0] + "," + pArr[1] + "|" + (p[0] + p[1]) % 10 + "|" + bArr[0] + "," + bArr[1] + "|" + (b[0] + b[1]) % 10;
    }
    else {
        return getBJLResultA(p, b, pArr, bArr);
    }
}

function getBJLResultP(p, b, pArr, bArr) {
    if ((b[0] + b[1]) % 10 > (p[0] + p[1] + p[2]) % 10) {
        return "B|" + pArr[0] + "," + pArr[1] + "," + pArr[2] + "|" + (p[0] + p[1] + p[2]) % 10 + "|" + bArr[0] + "," + bArr[1] + "|" + (b[0] + b[1]) % 10;
    }
    else if ((b[0] + b[1]) % 10 == (p[0] + p[1] + p[2]) % 10) {
        return "T|" + pArr[0] + "," + pArr[1] + "," + pArr[2] + "|" + (p[0] + p[1] + p[2]) % 10 + "|" + bArr[0] + "," + bArr[1] + "|" + (b[0] + b[1]) % 10;
    }
    else {
        return "P|" + pArr[0] + "," + pArr[1] + "," + pArr[2] + "|" + (p[0] + p[1] + p[2]) % 10 + "|" + bArr[0] + "," + bArr[1] + "|" + (b[0] + b[1]) % 10;
    }
}

function getBJLResultB(p, b, pArr, bArr) {
    if ((b[0] + b[1] + b[2]) % 10 > (p[0] + p[1]) % 10) {
        return "B|" + pArr[0] + "," + pArr[1] + "|" + (p[0] + p[1]) % 10 + "|" + bArr[0] + "," + bArr[1] + "," + bArr[2] + "|" + (b[0] + b[1] + b[2]) % 10;
    }
    else if ((b[0] + b[1] + b[2]) % 10 == (p[0] + p[1]) % 10) {
        return "T|" + pArr[0] + "," + pArr[1] + "|" + (p[0] + p[1]) % 10 + "|" + bArr[0] + "," + bArr[1] + "," + bArr[2] + "|" + (b[0] + b[1] + b[2]) % 10;
    }
    else {
        return "P|" + pArr[0] + "," + pArr[1] + "|" + (p[0] + p[1]) % 10 + "|" + bArr[0] + "," + bArr[1] + "," + bArr[2] + "|" + (b[0] + b[1] + b[2]) % 10;
    }
}

function getBJLResultA(p, b, pArr, bArr) {
    if ((b[0] + b[1] + b[2]) % 10 > (p[0] + p[1] + p[2]) % 10) {
        return "B|" + pArr[0] + "," + pArr[1] + "," + pArr[2] + "|" + (p[0] + p[1] + p[2]) % 10 + "|" + bArr[0] + "," + bArr[1] + "," + bArr[2] + "|" + (b[0] + b[1] + b[2]) % 10;
    }
    else if ((b[0] + b[1] + b[2]) % 10 == (p[0] + p[1] + p[2]) % 10) {
        return "T|" + pArr[0] + "," + pArr[1] + "," + pArr[2] + "|" + (p[0] + p[1] + p[2]) % 10 + "|" + bArr[0] + "," + bArr[1] + "," + bArr[2] + "|" + (b[0] + b[1] + b[2]) % 10;
    }
    else {
        return "P|" + pArr[0] + "," + pArr[1] + "," + pArr[2] + "|" + (p[0] + p[1] + p[2]) % 10 + "|" + bArr[0] + "," + bArr[1] + "," + bArr[2] + "|" + (b[0] + b[1] + b[2]) % 10;
    }
}

function getTRONUrl(type) {
    var tronurl = "";
    if (type == "en_US") {
        tronurl = process.env.VUE_APP_TRON_API;
    }
    if (type == "zh_CN") {
        tronurl = process.env.VUE_APP_TRONCN_API;
    }
    return tronurl;
}

export default {
    getNNResult,
    getDNResult,
    getBJLAllResult,
    getTRONUrl,
}