import Vue from 'vue'
import VueRouter from 'vue-router'
import LoginView from '../views/LoginView.vue'
import HomeView from '../views/HomeView.vue'
import ManualView from '../views/ManualView.vue'
import QueryView from '../views/QueryView.vue'
import DXGameView from '../views/Game/DXGameView.vue'
import DSGameView from '../views/Game/DSGameView.vue'
import DNGameView from '../views/Game/DNGameView.vue'
import BJLGameView from '../views/Game/BJLGameView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '*',
    component: LoginView
  },
  {
    path: '/',
    name: '',
    component: LoginView
  },
  {
    path: '/login',
    name: 'login',
    component: LoginView
  },
  {
    path: '/home',
    name: 'home',
    component: HomeView
  },
  {
    path: '/manual',
    name: 'manual',
    component: ManualView
  },
  {
    path: '/query',
    name: 'query',
    component: QueryView
  },
  {
    path: '/dxgame',
    name: 'dx',
    component: DXGameView
  },
  {
    path: '/dsgame',
    name: 'ds',
    component: DSGameView
  },
  {
    path: '/dngame',
    name: 'dn',
    component: DNGameView
  },
  {
    path: '/bjlgame',
    name: 'bjl',
    component: BJLGameView
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
