<template>
  <div class="page">
    <div style="position: absolute; left: 10px; top: 5px;font-size: 20px;" @click="home">
      <van-icon name="revoke" color="#aaa" class="animate__animated animate__fadeInLeft" />
    </div>
    <div class="page__hd page__hd1">
      <div class="page__title page__title1" @click="open">
        <div class="fadeInDown1">{{ walletShowAddress }}</div>
      </div>
      <div class="weui-flex" style="margin-top: 10px">
        <div class="weui-flex__item weui-flex__item1">
          <img width="36" src="../images/trx.png" class="flipInY" /><br />
          <div class="fadeInDown2">
            <countTo :startVal="startTrx" :endVal="endTrx" :duration="2000" :decimals="decimals"></countTo>
          </div>
        </div>
        <div class="weui-flex__item weui-flex__item1">
          <img width="36" src="../images/usdt.png" class="flipInY" /><br />
          <div class="fadeInDown2">
            <countTo :startVal="startUsdt" :endVal="endUsdt" :duration="2000" :decimals="decimals"></countTo>
          </div>
        </div>
      </div>
    </div>

    <div style="font-weight: bold">
      <van-notice-bar color="#333" background="#f5c822" left-icon="fire-o" mode="link" @click="service">
        {{ AD }}
      </van-notice-bar>
    </div>

    <div class="weui-cells" style="margin-top: 0; text-align: left">
      <div class="weui-cell weui-cell_access" @click="showBlock">
        <div class="weui-cell__bd" style="text-align: center; font-weight: bold">
          <div style="height: 24px; width: 100%">
            {{ content }}
          </div>
          <div class="weui-cell__desc">
            {{ result }}
          </div>
        </div>
      </div>
      <div class="weui-cell weui-cell_access weui-cell_access1" @click="showBlock">
        <div class="weui-cell__bd">
          <div style="height: 24px; width: 100%" v-if="isShow">
            <div style="float: left">{{ blockHashShow1 }}</div>
            <div class="showY">
              {{ blockHashShow2 }}
            </div>
            <div style="float: left">{{ blockHashShow3 }}</div>
          </div>
          <div style="height: 24px; width: 100%" v-if="isBJLShow">
            <div style="float: left">{{ blockHashShow1 }}</div>
            <div class="showB">
              {{ blockHashShow2 }}
            </div>
            <div class="showY">
              {{ blockHashShow22 }}
            </div>
            <div class="showR">
              {{ blockHashShow3 }}
            </div>
            <div class="showY">
              {{ blockHashShow33 }}
            </div>
          </div>
          <div style="height: 24px; width: 100%" v-if="isNNShow">
            <div style="float: left">{{ blockHashShow1 }}</div>
            <div class="showB">
              {{ blockHashShow2 }}
            </div>
            <div class="showR">
              {{ blockHashShow3 }}
            </div>
          </div>
          <div class="weui-cell__desc">
            {{ blockHashName }} | {{ blockNum }}
          </div>
        </div>
      </div>
      <div class="weui-cell weui-cell_access weui-cell_access1" @click="showTrade">
        <span class="weui-cell__bd">
          <span>{{ hashShow }}</span>
          <div class="weui-cell__desc">{{ hashName }}</div>
        </span>
      </div>

      <div class="weui-cell weui-cell_access weui-cell_access1" v-if="isPrize" @click="showPrize">
        <span class="weui-cell__bd">
          <span>{{ prizeShow }}</span>
          <div class="weui-cell__desc">{{ prizeName }}</div>
        </span>
      </div>
      <div class="weui-flex weui-flex1" v-if="isBJLShow">
        <div class="weui-flex__item">
          <div class="weui-cell weui-cell_access" style="border-right: 1px #eee solid">
            <span class="weui-cell__bd">
              <van-badge content="Win" v-if="playerShow">
                <div style="width: 70px">{{ playerResult }}</div>
              </van-badge>
              <span v-if="!playerShow">{{ playerResult }}</span>
              <div class="weui-cell__desc">{{ playerNum }}</div>
            </span>
          </div>
        </div>
        <div class="weui-flex__item">
          <div class="weui-cell weui-cell_access" style="border-right: 1px #eee solid">
            <span class="weui-cell__bd">
              <van-badge content="Win" v-if="bankerShow">
                <div style="width: 70px">{{ bankerResult }}</div>
              </van-badge>
              <span v-if="!bankerShow">{{ bankerResult }}</span>
              <div class="weui-cell__desc">{{ bankerNum }}</div>
            </span>
          </div>
        </div>
      </div>
      <div class="weui-flex weui-flex1" v-if="isNNShow">
        <div class="weui-flex__item">
          <div class="weui-cell weui-cell_access" style="border-right: 1px #eee solid">
            <span class="weui-cell__bd">
              <van-badge content="Win" v-if="playerNNShow">
                <div style="width: 115px">{{ playerNNResult }}</div>
              </van-badge>
              <span v-if="!playerNNShow">{{ playerNNResult }}</span>
              <div class="weui-cell__desc">{{ playerNNNum }}</div>
            </span>
          </div>
        </div>
        <div class="weui-flex__item">
          <div class="weui-cell weui-cell_access" style="border-right: 1px #eee solid">
            <span class="weui-cell__bd">
              <van-badge content="Win" v-if="bankerNNShow">
                <div style="width: 115px">{{ bankerNNResult }}</div>
              </van-badge>
              <span v-if="!bankerNNShow">{{ bankerNNResult }}</span>
              <div class="weui-cell__desc">{{ bankerNNNum }}</div>
            </span>
          </div>
        </div>
      </div>
      <div class="weui-flex weui-flex1">
        <div class="weui-flex__item">
          <div class="weui-cell weui-cell_access" style="border-right: 1px #eee solid">
            <span class="weui-cell__bd">
              <span>{{ bet }} {{ betResult }}</span>
              <div class="weui-cell__desc">{{ betResultName }}</div>
            </span>
          </div>
        </div>
        <div class="weui-flex__item">
          <div class="weui-cell weui-cell_access" style="border-right: 1px #eee solid">
            <span class="weui-cell__bd">
              <span>{{ betNum }} {{ type }}</span>
              <div class="weui-cell__desc">{{ betNumName }}</div>
            </span>
          </div>
        </div>
      </div>
      <div class="weui-flex weui-flex1">
        <div class="weui-flex__item">
          <div class="weui-cell weui-cell_access" style="border-right: 1px #eee solid">
            <span class="weui-cell__bd">
              <span>{{ draw }} {{ drawResult }}</span>
              <div class="weui-cell__desc">{{ drawName }}</div>
            </span>
          </div>
        </div>
        <div class="weui-flex__item">
          <div class="weui-cell weui-cell_access">
            <span class="weui-cell__bd">
              <span>{{ drawNum }} {{ type }}</span>
              <div class="weui-cell__desc">{{ drawNumName }}</div>
            </span>
          </div>
        </div>
      </div>
    </div>
    <div class="weui-flex weui-flex2" style="margin-bottom: 60px">
      <div class="weui-flex__item">
        <button role="button" class="weui-btn weui-btn_primary" style="background: #333; color: #f5c822; width: 90%" @click="gobet()">
          {{ this.$t("game.continue") }}
        </button>
      </div>
      <div class="weui-flex__item">
        <button role="button" class="weui-btn weui-btn_default" style="background: #f5c822; color: #333; width: 90%" @click="home()">
          {{ this.$t("game.back") }}
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "QueryView",
  data() {
    return {
      tronWeb: null,
      jadd: process.env.VUE_APP_JADD,
      uadd: process.env.VUE_APP_UADD,
      dxgame: process.env.VUE_APP_DXADD,
      dsgame: process.env.VUE_APP_DSADD,
      dngame: process.env.VUE_APP_DNADD,
      bjlgame: process.env.VUE_APP_BJLADD,
      game: null,

      tronurl: this.$t("home.tron"),
      wallet: this.$t("home.wallet"),
      telegram: this.$t("home.telegram"),

      walletShowAddress: "",
      walletAddress: "",
      trxbalance: 0,
      usdtbalance: 0,
      amount: 0,
      owner_address: "",
      to_address: "",
      contract_address: "",

      startUsdt: 0,
      endUsdt: 0,
      startTrx: 0,
      endTrx: 0,
      decimals: 2,
      sum: 1000000,

      AD: this.$t("home.AD"),

      dxodds: 1.95,
      dsodds: 1.98,

      bodds: 1.95,
      podds: 2,
      todds: 8,
      nodds: 5,
      odds: 0.95,
      playerResult: "A,5,6",
      bankerResult: "1,5,6",
      playerNum: this.$t("game.player") + " 1",
      bankerNum: this.$t("game.banker") + " 2",
      bankerShow: true,
      playerShow: false,

      playerNNResult: "A,9,1 + 5,6",
      bankerNNResult: "B,C,D + 5,7",
      playerNNNum: this.$t("game.player") + " " + this.$t("game.bull") + " 1",
      bankerNNNum: this.$t("game.banker") + " " + this.$t("game.bull") + " 2",
      bankerNNShow: true,
      playerNNShow: false,

      isShow: true,
      isBJLShow: false,
      isNNShow: false,

      result: "Result",
      content: 0,

      bet: "",
      betResultName: this.$t("game.betResultName"),
      betNum: 0,
      betResult: 0,
      betNumName: this.$t("game.betNumName"),
      draw: "0",
      drawName: this.$t("game.drawName"),
      drawNum: 0,
      drawResult: "",
      drawNumName: this.$t("game.drawNumName"),

      blockNum: "00000000",
      blockHashName: this.$t("game.blockHashName"),
      blockHashShow1: "000000000000000000000000000000",
      blockHashShow2: "",
      blockHashShow22: "",
      blockHashShow3: "",
      blockHashShow33: "",
      blockHash: "",

      hashName: this.$t("game.hashName"),
      hashShow: "000000000000000000000000000000",
      hash: this.$route.query.txid,

      isPrize: false,
      prizeName: this.$t("game.prizeName"),
      prizeShow: "000000000000000000000000000000",
      prize: "",

      Inval: null,
      message1: this.$t("game.message1"),
      message2: this.$t("game.message2"),
      message3: this.$t("game.message3"),

      type: "",
      way: "",
    };
  },
  beforeCreate() {
    if (this.$route.query.l == "en_US") this.$i18n.locale = "en_US";
    if (this.$route.query.l == "zh_CN") this.$i18n.locale = "zh_CN";
  },
  mounted() {
    this.initQuery();
  },
  beforeDestroy() {
    clearInterval(this.Inval);
    this.$toast.clear();
  },
  methods: {
    initQuery: function () {
      this.$toast.loading({
        message: this.message3,
        forbidClick: true,
        duration: 0,
      });
      this.game = [this.dxgame, this.dsgame, this.dngame, this.bjlgame];
      this.linkWallet();
    },
    showTrade: function () {
      window.open(this.$gameresult.getTRONUrl(this.$i18n.locale) + "transaction/" + this.hash, "_blank");
    },
    showPrize: function () {
      window.open(this.$gameresult.getTRONUrl(this.$i18n.locale) + "transaction/" + this.prize, "_blank");
    },
    showBlock: function () {
      window.open(this.$gameresult.getTRONUrl(this.$i18n.locale) + "block/" + this.blockNum, "_blank");
    },
    open: function () {
      window.open(this.$gameresult.getTRONUrl(this.$i18n.locale) + "address/" + this.walletAddress, "_blank");
    },
    home: function () {
      this.$router.push({ path: "/home?l=" + this.$i18n.locale });
    },
    service: function () {
      window.open(this.telegram, "_blank");
    },
    gobet: function () {
      this.$router.push({ path: this.way + "?l=" + this.$i18n.locale });
    },
    getShowHash: function (hash) {
      return hash.substr(0, 12) + "********" + hash.substr(-15, 15);
    },
    getShowAddress: function (add) {
      return add.substr(0, 6) + "********" + add.substr(-6, 6);
    },
    getShowNum: function (num) {
      return Math.floor(num * 100) / 100;
    },
    getPrizeHash(txid) {
      this.$gamerequest
        .getBlock({ t: txid })
        .then((response) => {
          var data = response.data.data;
          if (data.t != "") {
            this.isPrize = true;
            this.prize = data.t;
            this.prizeShow = this.getShowHash(this.prize);
          }
        });
    },
    linkWallet: function () {
      var i = 0;
      this.Inval = setInterval(() => {
        if (window.tronWeb != undefined) {
          var wadd = window.tronWeb.defaultAddress.base58;
          this.walletAddress = wadd ? wadd : "";

          if (this.walletAddress) {
            window.clearInterval(this.Inval);
            this.tronWeb = window.tronWeb;
            this.walletShowAddress = this.getShowAddress(this.walletAddress);
            this.getTransaction(this.hash);
            this.getBalance(this.walletAddress);
          }
        }

        if (i > 5) {
          window.clearInterval(this.Inval);
          this.$router.push({ path: "/login?l=" + this.$i18n.locale });
        }
        i++;
      }, 1000);
    },
    getBalance: function (add) {
      this.$gamerequest.getAccount(add).then((response) => {
        var data = response.data.data;
        data.forEach((element) => {
          if (element.tokenId == "_") {
            this.trxbalance = element.balance / this.sum;
            this.startTrx = this.endTrx;
            this.endTrx = this.getShowNum(this.trxbalance);
          }
          if (element.tokenId == this.uadd) {
            this.usdtbalance = element.balance / this.sum;
            this.startUsdt = this.endUsdt;
            this.endUsdt = this.getShowNum(this.usdtbalance);
          }
        });
      });
    },
    getTransactionInfo: async function (txid) {
      await this.tronWeb.trx.getTransactionInfo(txid).then((output) => {
        if (output.blockNumber !== undefined)
          this.getBlockByNumber(output.blockNumber);
        else {
          this.showErr();
        }
      });
    },
    getBlockByNumber: async function (block) {
      await this.tronWeb.trx.getBlockByNumber(block).then((output) => {
        this.blockNum = block;
        this.blockHash = output.blockID;
        this.hashShow = this.getShowHash(this.hash);
        this.initResult();
        this.getPrizeHash(this.hash);
        this.$toast.clear();
      });
    },
    getTransaction: async function (txid) {
      await this.tronWeb.trx
        .getTransaction(txid)
        .then((output) => {

          if (output.ret[0].contractRet != "SUCCESS") {
            this.showErr();
          }
          var data = output.raw_data.contract[0];
          if (data.type == "TransferContract") {
            var amount = data.parameter.value.amount / this.sum;
            this.amount = this.getShowNum(amount);
            var owner = data.parameter.value.owner_address;
            this.owner_address = this.tronWeb.address.fromHex(owner);
            var to_address = data.parameter.value.to_address;
            this.to_address = this.tronWeb.address.fromHex(to_address);
            if (this.owner_address == this.walletAddress && this.amount >= 10) {
              this.type = "TRX";
              if (this.game.includes(this.to_address)) {
                this.getTransactionInfo(txid);
              } else {
                this.showErr();
              }
            } else {
              this.showErr();
            }
          }
          if (data.type == "TriggerSmartContract") {
            var result = this.decodeParams(
              ["address", "uint256"],
              data.parameter.value.data,
              true
            );

            Promise.all([result]).then((dataArray) => {
              var amount = this.tronWeb.toDecimal(dataArray[0][1]._hex);
              this.amount = this.getShowNum(amount / this.sum);
              var owner = data.parameter.value.owner_address;
              this.owner_address = this.tronWeb.address.fromHex(owner);
              this.to_address = this.tronWeb.address.fromHex(dataArray[0][0]);
              var contract = data.parameter.value.contract_address;
              this.contract_address = this.tronWeb.address.fromHex(contract);

              if (this.owner_address == this.walletAddress && this.contract_address == this.uadd && this.amount >= 10) {
                this.type = "USDT";
                if (this.game.includes(this.to_address)) {
                  this.getTransactionInfo(txid);
                } else {
                  this.showErr();
                }
              } else {
                this.showErr();
              }
            });
          }
        })
        .catch(() => {
          this.showErr();
        });
    },
    decodeParams: async function (types, output, ignoreMethodHash) {
      var ethers = require("ethers");
      const AbiCoder = ethers.utils.AbiCoder;
      const ADDRESS_PREFIX = "41";
      if (!output || typeof output === "boolean") {
        ignoreMethodHash = output;
        output = types;
      }

      if (ignoreMethodHash && output.replace(/^0x/, "").length % 64 === 8)
        output = "0x" + output.replace(/^0x/, "").substring(8);

      const abiCoder = new AbiCoder();

      if (output.replace(/^0x/, "").length % 64)
        throw new Error(
          "The encoded string is not valid. Its length must be a multiple of 64."
        );
      return abiCoder.decode(types, output).reduce((obj, arg, index) => {
        if (types[index] == "address")
          arg = ADDRESS_PREFIX + arg.substr(2).toLowerCase();
        obj.push(arg);
        return obj;
      }, []);
    },
    getLastNum: function (blockHash) {
      var b = blockHash.split("");
      for (var i = b.length; i > 0; i--) {
        var index = i - 1;
        if (!isNaN(b[index])) {
          this.blockHashShow1 =
            this.blockHash.substr(0, 12) +
            "********" +
            this.blockHash.substr(-15, 15 - (b.length - index));
          this.blockHashShow2 = b[index];
          this.blockHashShow3 = blockHash.substr(i, b.length - i);
          return b[index];
        }
      }
    },
    getBull: function (blockHash) {
      var p = this.$gameresult.getNNResult(blockHash.substr(-10, 5)).split("|");
      var b = this.$gameresult.getNNResult(blockHash.substr(-5, 5)).split("|");
      var r = this.$gameresult.getDNResult(p, b);

      this.playerNNResult = p[2];
      this.playerNNNum =
        this.$t("game.player") + " " + this.$t("game.bull") + p[1];
      this.bankerNNResult = b[2];
      this.bankerNNNum =
        this.$t("game.banker") + " " + this.$t("game.bull") + b[1];

      if (parseInt(p[1]) == 10)
        this.playerNNNum = this.$t("game.player") + " " + this.$t("game.ybull");
      if (parseInt(p[1]) == -1)
        this.playerNNNum = this.$t("game.player") + " " + this.$t("game.nbull");

      if (parseInt(b[1]) == 10)
        this.bankerNNNum = this.$t("game.banker") + " " + this.$t("game.ybull");
      if (parseInt(b[1]) == -1)
        this.bankerNNNum = this.$t("game.banker") + " " + this.$t("game.nbull");

      this.blockHashShow1 =
        this.blockHash.substr(0, 12) +
        "********" +
        this.blockHash.substr(-16, 6);
      this.blockHashShow2 = this.blockHash.substr(-10, 5);
      this.blockHashShow3 = this.blockHash.substr(-5, 5);
      return r;
    },
    getBaccarat: function (blockHash) {
      var baccarat = this.$gameresult.getBJLAllResult(blockHash).split("|");

      this.playerResult = baccarat[1];
      this.playerNum = this.$t("game.player") + " " + baccarat[2];
      this.bankerResult = baccarat[3];
      this.bankerNum = this.$t("game.banker") + " " + baccarat[4];

      this.blockHashShow1 =
        this.blockHash.substr(0, 12) +
        "********" +
        this.blockHash.substr(-16, 10);
      this.blockHashShow2 = this.blockHash.substr(-6, 2);
      this.blockHashShow22 = this.blockHash.substr(-4, 1);
      this.blockHashShow3 = this.blockHash.substr(-3, 2);
      this.blockHashShow33 = this.blockHash.substr(-1, 1);
      return baccarat[0];
    },
    showErr: function () {
      this.$router.push({ path: "/home?l=" + this.$i18n.locale });
    },
    initResult: function () {
      this.betNum = this.amount;
      //dx
      if (this.to_address == this.dxgame) {
        this.getDXResult();
      }
      //ds
      if (this.to_address == this.dsgame) {
        this.getDSResult();
      }
      //dn
      if (this.to_address == this.dngame) {
        this.getDNResult();
      }
      //bjlgame
      if (this.to_address == this.bjlgame) {
        this.getBJLResult();
      }
    },
    getDXResult: function () {
      this.way = "/dxgame";
      this.betResult = this.betNum % 10;
      if (this.betResult > 4) {
        this.bet = this.$t("game.big");
      } else {
        this.bet = this.$t("game.small");
      }
      this.betResult = "(" + this.betResult + ")";

      this.drawResult = this.getLastNum(this.blockHash);
      if (this.drawResult > 4) {
        this.draw = this.$t("game.big");
      } else {
        this.draw = this.$t("game.small");
      }
      this.drawResult = "(" + this.drawResult + ")";

      if (this.bet == this.draw) {
        this.drawNum = this.getShowNum(this.betNum * this.dxodds);
        this.result = this.$t("game.win");
        this.content = "+ " + this.drawNum + " " + this.type;
      } else {
        this.drawNum = 0;
        this.result = this.$t("game.lose");
        this.content = "- " + this.betNum + " " + this.type;
      }
    },
    getDSResult: function () {
      this.way = "/dsgame";
      this.betResult = this.betNum % 10;
      if (this.betResult % 2 == 0) {
        this.bet = this.$t("game.even");
      } else {
        this.bet = this.$t("game.odd");
      }
      this.betResult = "(" + this.betResult + ")";

      this.drawResult = this.getLastNum(this.blockHash);
      if (this.drawResult % 2 == 0) {
        this.draw = this.$t("game.even");
      } else {
        this.draw = this.$t("game.odd");
      }
      this.drawResult = "(" + this.drawResult + ")";

      if (this.bet == this.draw) {
        this.drawNum = this.getShowNum(this.betNum * this.dsodds);
        this.result = this.$t("game.win");
        this.content = "+ " + this.drawNum + " " + this.type;
      } else {
        this.drawNum = 0;
        this.result = this.$t("game.lose");
        this.content = "- " + this.betNum + " " + this.type;
      }
    },
    getDNResult: function () {
      this.way = "/dngame";
      this.isNNShow = true;
      this.isShow = false;
      this.betResult = this.betNum % 10;
      if (this.betResult == 8 || this.betResult == 9) {
        this.bet = this.$t("game.tie");
      } else {
        if (this.betResult % 2 == 0) {
          this.bet = this.$t("game.banker");
        } else {
          this.bet = this.$t("game.player");
        }
      }
      this.betResult = "(" + this.betResult + ")";

      var bull = this.getBull(this.blockHash);
      if (bull == "B") {
        this.draw = this.$t("game.banker");
        this.bankerNNShow = true;
        this.playerNNShow = false;
      }
      if (bull == "T") {
        this.draw = this.$t("game.tie");
        this.bankerNNShow = false;
        this.playerNNShow = false;
      }
      if (bull == "P") {
        this.draw = this.$t("game.player");
        this.bankerNNShow = false;
        this.playerNNShow = true;
      }

      if (this.bet == this.draw) {
        if (this.bet == this.$t("game.banker"))
          this.drawNum = this.getShowNum(this.betNum * this.bodds);
        if (this.bet == this.$t("game.tie"))
          this.drawNum = this.getShowNum(this.betNum * this.nodds);
        if (this.bet == this.$t("game.player"))
          this.drawNum = this.getShowNum(this.betNum * this.podds);
        this.result = this.$t("game.win");
        this.content = "+ " + this.drawNum + " " + this.type;
      } else {
        if (this.draw == this.$t("game.tie")) {
          this.drawNum = this.getShowNum(this.betNum * this.odds);
          this.result = this.$t("game.draw");
          this.content = "+ " + this.drawNum + " " + this.type;
        } else {
          this.drawNum = 0;
          this.result = this.$t("game.lose");
          this.content = "- " + this.betNum + " " + this.type;
        }
      }
    },
    getBJLResult: function () {
      this.way = "/bjlgame";
      this.isBJLShow = true;
      this.isShow = false;
      this.betResult = this.betNum % 10;
      if (this.betResult == 8 || this.betResult == 9) {
        this.bet = this.$t("game.tie");
      } else {
        if (this.betResult % 2 == 0) {
          this.bet = this.$t("game.banker");
        } else {
          this.bet = this.$t("game.player");
        }
      }
      this.betResult = "(" + this.betResult + ")";

      var baccarat = this.getBaccarat(this.blockHash);
      if (baccarat[0] == "B") {
        this.draw = this.$t("game.banker");
        this.bankerShow = true;
        this.playerShow = false;
      }
      if (baccarat[0] == "T") {
        this.draw = this.$t("game.tie");
        this.bankerShow = false;
        this.playerShow = false;
      }
      if (baccarat[0] == "P") {
        this.draw = this.$t("game.player");
        this.bankerShow = false;
        this.playerShow = true;
      }

      if (this.bet == this.draw) {
        if (this.bet == this.$t("game.banker"))
          this.drawNum = this.getShowNum(this.betNum * this.bodds);
        if (this.bet == this.$t("game.tie"))
          this.drawNum = this.getShowNum(this.betNum * this.todds);
        if (this.bet == this.$t("game.player"))
          this.drawNum = this.getShowNum(this.betNum * this.podds);
        this.result = this.$t("game.win");
        this.content = "+ " + this.drawNum + " " + this.type;
      } else {
        if (this.draw == this.$t("game.tie")) {
          this.drawNum = this.getShowNum(this.betNum * this.odds);
          this.result = this.$t("game.draw");
          this.content = "+ " + this.drawNum + " " + this.type;
        } else {
          this.drawNum = 0;
          this.result = this.$t("game.lose");
          this.content = "- " + this.betNum + " " + this.type;
        }
      }
    },
  },
};
</script>
