import axios from 'axios'

const service = axios.create({
  baseURL: process.env.VUE_APP_BASE_API,
  timeout: 10000
})

const tronservice = axios.create({
  baseURL: process.env.VUE_APP_SCAN_API,
  timeout: 10000
})

function getBlock(data) {
  return service({
    url: '/user/getblock',
    method: 'post',
    data
  })
}

function getAccount(address) {
  return tronservice({
    url: '/api/account/tokens?address=' + address,
    method: 'get',
  })
}

export default {
  getBlock,
  getAccount
}


